
import { useEffect, useRef, useState } from 'react';
import './App.scss';
import axios from 'axios';
import check from "./check.png"
import Time from './components/time/time';
import gear from "./gear.png"
import gearb from "./gearb.png"
import cheerio from 'cheerio';
import { parse as parseUrl } from 'url';
import close from "./cc.png"
import closeb from "./cbb.png"
import Favs from './components/favourites/favourites';
import moon from "./moon.png"
import sun from "./sun.png"
import pic1 from "./images/pic1.jpg"
import pic2 from "./images/pic2.jpg"
import pic3 from "./images/pic3.jpg"
import pic4 from "./images/pic4.jpg"
import pic5 from "./images/pic5.jpg"
import pic6 from "./images/pic6.jpg"

function App() {


  const [time, setTime] = useState(localStorage.getItem("time") ? localStorage.getItem("time") : "show");
  const [dateD, setdateD] = useState(localStorage.getItem("dateD") ? localStorage.getItem("dateD") : "show");
  const [weatherD, setweatherD] = useState(localStorage.getItem("weatherD") ? localStorage.getItem("weatherD") : "show");
  const [favoritesD, setfavoritesD] = useState(localStorage.getItem("favoritesD") ? localStorage.getItem("favoritesD") : "show");
  const [theme, setTheme] = useState(
    localStorage.getItem("theme") ?
      (
        localStorage.getItem("theme") !== null
          ? (
            localStorage.getItem("theme") == "light"
              ? "light"
              : (
                localStorage.getItem("theme") == "dark"
                  ? "dark"
                  : localStorage.getItem("theme") == "custom" && localStorage.getItem("customImg")
                    ? "custom"
                    : "dark"
              )

          )
          : "dark"
      )
      : "dark"
  )

  // Check if the theme is custom and a custom image is provided
  if (theme === "custom" && localStorage.getItem("customImg")) {
    const img = new Image();
    img.src = localStorage.getItem("customImg");
    img.addEventListener('load', function () {
      // Image loaded successfully, do nothing
    });
    img.addEventListener('error', function () {
      // Image failed to load, set theme to "dark"
      setTheme("dark");
    });
  }

  const backs = [
    {
      "name": pic1
    },
    {
      "name": pic2
    },
    {
      "name": pic3
    },
    {
      "name": pic4
    },
    {
      "name": pic5
    },
    {
      "name": pic6
    }
  ]

  const [customImg, setCustomImg] = useState(
    localStorage.getItem("customImg") ? localStorage.getItem("customImg") : null
  );

  const handleCustomImg = (name) => {
    setCustomImg(name);
    setTheme("custom")
    localStorage.setItem("theme", "custom");
    localStorage.setItem("customImg", name);
    closeSettings()
  };

  useEffect(() => {
    console.log(customImg);
    console.log(localStorage.getItem("customImg"))
  }, [customImg]);

  const changeTheme = () => {
    if (theme == "light") {
      setTheme("dark")
      localStorage.setItem("theme", "dark")
    }
    else if (theme == "dark") {
      setTheme("light")
      localStorage.setItem("theme", "light")
    }
    else {
      setTheme("light")
      localStorage.setItem("theme", "light")
    }
  }




  const handleDisplayTimeChange = () => {
    if (time == "show") {
      setTime("hide")
      localStorage.setItem("time", "hide")
    }
    else if (time == "hide") {
      setTime("show")
      localStorage.setItem("time", "show")
    }
    else {
      setTime("hide")
      localStorage.setItem("time", "hide")
    }
  };


  const handleDisplayWeatherChange = () => {
    if (weatherD == "show") {
      setweatherD("hide")
      localStorage.setItem("weatherD", "hide")
    }
    else if (weatherD == "hide") {
      setweatherD("show")
      localStorage.setItem("weatherD", "show")
    }
    else {
      setweatherD("hide")
      localStorage.setItem("weatherD", "hide")
    }
  };

  const handleDisplayDateChange = () => {
    if (dateD == "show") {
      setdateD("hide")
      localStorage.setItem("dateD", "hide")
    }
    else if (dateD == "hide") {
      setdateD("show")
      localStorage.setItem("dateD", "show")
    }
    else {
      setdateD("hide")
      localStorage.setItem("dateD", "hide")
    }
  };

  const handleDisplayFavoritesChange = () => {
    if (favoritesD == "show") {
      setfavoritesD("hide")
      localStorage.setItem("favoritesD", "hide")
    }
    else if (favoritesD == "hide") {
      setfavoritesD("show")
      localStorage.setItem("favoritesD", "show")
    }
    else {
      setfavoritesD("hide")
      localStorage.setItem("favoritesD", "hide")
    }
  };


  const openSettings = () => {
    document.getElementById("setting").style.display = "flex"
  }
  const closeSettings = () => {
    document.getElementById("setting").style.display = "none"
  }

  const settingRef = useRef(null)

  useEffect(() => {
    const customImageFromLocalStorage = localStorage.getItem("customImg");
    if (customImageFromLocalStorage) {
      setCustomImg(customImageFromLocalStorage);
    }
  }, []);

  useEffect(() => {
    function handleClickOutside(event) {
      if (settingRef.current && !settingRef.current.contains(event.target)) {
        closeSettings()
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const [activeSetting, setActiveSetting] = useState("features")


  const removePhoto = () => {
    setTheme("dark")
    localStorage.setItem("theme", "dark")
    localStorage.setItem("customImg", null)
    setCustomImg(null)
    closeSettings()
  }





  return (
    <>

      <div id="setting" className="add-favs add-favs-light">

        <div ref={settingRef} className={theme == "light" ? "add-favs-box sbox add-favs-box-light" : "sbox add-favs-box"}>
          <div className="add-favs-head">
            <p className={theme == "light" ? "head-add head-add-light" : "head-add"}>Settings</p>
            <img className="close" onClick={closeSettings} src={theme == "light" ? closeb : close} />
          </div>
          <div className='settings-box'>
            <div className='settings-box-left'>
              <p onClick={() => { setActiveSetting("features") }} className={
                theme == "light"
                  ? (
                    activeSetting == "features"
                      ? "feature feature-light activeFeature-light"
                      : "feature feature-light"

                  )
                  : (
                    activeSetting == "features"
                      ? "feature activeFeature"
                      : "feature"
                  )
              }>Features</p>
              <p onClick={() => { setActiveSetting("backgrounds") }} className={
                theme == "light"
                  ? (
                    activeSetting == "backgrounds"
                      ? "feature feature-light activeFeature-light"
                      : "feature feature-light"

                  )
                  : (
                    activeSetting == "backgrounds"
                      ? "feature activeFeature"
                      : "feature"
                  )
              }>Backgrounds</p>
            </div>
            <div className='settings-box-right'>
              {
                activeSetting == "features"
                  ? (
                    <div className='sbox-checks'>
                      <div className={theme == "light" ? "sbox-check sbox-check-light" : "sbox-check"}>
                        <p className={theme == "light" ? "sbox-check-text sbox-check-text-light" : "sbox-check-text"}>Time</p>
                        <input type='checkbox' className='checkox' checked={time == "show"} onChange={handleDisplayTimeChange} />
                      </div>
                      <div className={theme == "light" ? "sbox-check sbox-check-light" : "sbox-check"}>
                        <p className={theme == "light" ? "sbox-check-text sbox-check-text-light" : "sbox-check-text"}>Date</p>
                        <input type='checkbox' className='checkox' checked={dateD == "show"} onChange={handleDisplayDateChange} />
                      </div>
                      <div className={theme == "light" ? "sbox-check sbox-check-light" : "sbox-check"}>
                        <p className={theme == "light" ? "sbox-check-text sbox-check-text-light" : "sbox-check-text"}>Weather</p>
                        <input type='checkbox' className='checkox' checked={weatherD == "show"} onChange={handleDisplayWeatherChange} />
                      </div>
                      <div className={theme == "light" ? "sbox-check sbox-check-light" : "sbox-check"}>
                        <p className={theme == "light" ? "sbox-check-text sbox-check-text-light" : "sbox-check-text"}>Favorites</p>
                        <input type='checkbox' className='checkox' checked={favoritesD == "show"} onChange={handleDisplayFavoritesChange} />
                      </div>
                    </div>
                  )
                  : <div className='head-right-set'>
                    <button onClick={removePhoto} disabled={customImg == null} className='remove-photo'>Remove Photo</button>
                    <div className='back-images' >
                      {
                        backs.map((b) => (
                          <div className='bimgbox'>
                            <img className='bimg' onClick={() => { handleCustomImg(b.name) }} src={b.name} />
                          </div>
                        ))
                      }
                    </div>
                  </div>
              }
            </div>
          </div>
        </div>
      </div>




      <div style={theme == "custom" ? { backgroundImage: `url(${customImg})` } : null} className={theme == "light" ? 'App App-light' : theme == "custom" ? "App App-custom" : "App"} >
        <div className='AppRight'>
          {
            favoritesD == "show" ? <Favs theme={theme} /> : null
          }
        </div>
        <div className='AppLeft'>
          {
            (time == "hide" && dateD == "hide" && weatherD == "hide") ? <div></div> : <Time timeD={time} theme={theme} weatherD={weatherD} dateD={dateD} />
          }
          <div className='setting-box'>
            {
              theme == "custom" ? null : (
                <div className={theme == "light" ? "setting-img setting-img-light" : theme == "custom" ? "setting-img customcss" : "setting-img"}>

                  <img className='setting' onClick={changeTheme} src={theme == "dark" ? sun : moon} />
                </div>
              )
            }
            <div onClick={openSettings} className={theme == "light" ? "setting-img setting-img-light" : theme == "custom" ? "setting-img customcss" : "setting-img"}>

              <img className='setting' src={theme == "light" ? gearb : gear} />
            </div>

          </div>
        </div>
      </div>
    </>
  );
}

export default App;
